<template>
	<div class="header-box">
		<div class="content-box flex-row">
			<div class="logo-box flex-row">
				<img class="logo-img" :src="pageState.logo" alt="">
				<span>{{pageState.companyAbbr}}</span>
			</div>
			<!-- <img class="logo-img" src="@/assets/logo.png" alt="" @click="goIndex"> -->
			<Nav :list="pageState.navList"/>
		</div>
	</div>
</template>

<script>
import { defineComponent, onMounted, ref, watchEffect } from 'vue'
import { useRouter } from 'vue-router' 
import Nav from '@/components/nav.vue'
import { selectMenuList } from '@/api/api.js'
export default defineComponent({
	components: {
		Nav
	},
	props: {
		info: Object
	},
	setup (props) {
		const router = useRouter()
		const pageState = ref({
			navList: []
		})
		watchEffect(() => {
			if (localStorage.getItem('company') && localStorage.getItem('company') != 'undefined') {
				let companyInfo = JSON.parse(localStorage.getItem('company'))
				pageState.value.logo = companyInfo.extras.filter(item => { return item.extraName == 'LOGO'})[0].extraValue
				pageState.value.companyAbbr = companyInfo.extras.filter(item => { return item.extraName == '公司简称'})[0].extraValue
			}
			if (props.info && props.info.length) {
				let companyInfo = props.info
				pageState.value.logo = companyInfo.extras.filter(item => { return item.extraName == 'LOGO'})[0].extraValue
				pageState.value.companyAbbr = companyInfo.extras.filter(item => { return item.extraName == '公司简称'})[0].extraValue
			}
		})
		
		onMounted(() => {
			selectMenuList().then((res) => {
				if (res.code === 200) {
					pageState.value.navList = res.data
					localStorage.setItem('navList', JSON.stringify(res.data))
				}
			})
		})
		const goIndex = () => {
			router.push('/')
		}
		return {
			pageState,
			goIndex
			
		}
	}
})
</script>

