<template>
	<div class="footer-box">
		<div class="content-box flex-row">
			<div class="logo-box">
				<img class="logo-img" :src="pageState.logo" alt="">
				<span>{{pageState.companyAbbr}}</span>
			</div>
			<!-- 网站导航 -->
			<div class="map-box">
				<div>网站导航</div>
				<div>
					<span 
						class="map"
						v-for="(item, index) of pageState.navList" 
						:key="index"
						@click="onNavClick(index, item)"
					>
						{{item.menuName}}
					</span>
				</div>
			</div>
			<!-- 联系方式 -->
			<div class="link-box">
				<div>联系方式</div>
				<div>
					<span>{{pageState.companyName}}</span>
					<div>
						<span v-if="pageState.phone">电话：{{pageState.phone}}</span>
						<span v-if="pageState.email">邮箱：{{pageState.email}}</span>
					</div>
					<span v-if="pageState.address">地址：{{pageState.address}}</span>
				</div>
			</div>
			<!-- 二维码 -->
			<div class="qr-code" v-if="pageState.qrcode">
				<img src="@/assets/qrcode.png" alt="">
				<span>（扫描微信公众号）</span>
			</div>
		</div>
		<!-- 备案号 -->
		<div class="no-box">
			版权所有：{{pageState.companyName}} - 鲁ICP备13006208号-1  鲁公网安备 37088202000031号
		</div>
	</div>
</template>

<script>
import { defineComponent, onMounted, watchEffect, ref } from 'vue'
import { useRouter } from 'vue-router' 
import { selectMenuList } from '@/api/api.js'
export default defineComponent({
	props: {
		info: Object
	},
	setup (props) {
		const router = useRouter()
		const pageState = ref({
			companyName: ''
		})
		watchEffect(() => {
			if (localStorage.getItem('company') && localStorage.getItem('company') != 'undefined') {
				let companyInfo = JSON.parse(localStorage.getItem('company'))
				pageState.value.companyName = companyInfo.companyName
				companyInfo.extras.map(item => {
					switch (item.extraName){
						case '联系电话':
							pageState.value.phone = item.extraValue
							break;
						case '邮箱':
							pageState.value.email = item.extraValue
							break;
						case '地址':
							pageState.value.address = item.extraValue
							break;
						case 'LOGO':
							pageState.value.logo = item.extraValue
							break;
						case '公司简称':
							pageState.value.companyAbbr = item.extraValue
							break;
						default:
							break;
					}
				})
			}
			if (props.info && props.info.length) {
				let extras = props.info.extras
				pageState.value.companyName = props.info.companyName
				extras.map(item => {
					switch (item.paramType){
						case '联系电话':
							pageState.value.phone = item.extraValue
							break;
						case '邮箱':
							pageState.value.email = item.extraValue
							break;
						case '地址':
							pageState.value.address = item.extraValue
							break;
						case 'LOGO':
							pageState.value.logo = item.extraValue
							break;
						case '公司简称':
							pageState.value.companyAbbr = item.extraValue
							break;
						default:
							break;
					}
				})
			}
		})
		
		// if (props.info) {
		
		// }
		onMounted(() => {
			selectMenuList().then((res) => {
				if (res.code === 200) {
					pageState.value.navList = res.data
				}
			})
		})
		const onNavClick = (index, item) => {
			localStorage.setItem('type', 0)
			localStorage.setItem('nav', index)
			if (index == 0) {
				router.push({path: '/', query: { id: item.menuId }})
			} else if (index == 6) {
				router.push('/contact')
			} else {
				router.push({
					path: '/content', 
					query: {
						type: index,
						id: item.menuId,
						name: item.menuName
					}
				})
			}
		}
		return {
			pageState,
			onNavClick
		}
	}
})
</script>

