<template>
	<div class="nav-box flex-row">
		<div 
			v-for="(item, index) of pageState.nav"
			:key="index"
			:class="pageState.active == index ? 'nav-item active' : 'nav-item'"
			@click="onNavClick(index, item)"
		>
			{{item.menuName}}
		</div>
	</div>
</template>

<script>
import { defineComponent, ref, watchEffect } from 'vue'
import { useRouter } from 'vue-router'
import eventBus from '@/utils/eventBus.js'
export default defineComponent({
	props: {
		list: Array
	},
	setup (props) {
		const router = useRouter()
		const pageState = ref({
			nav: [],
			active: 0
		})
		eventBus.$on('changeNav', (res) => {
			pageState.value.active = res
		})
		watchEffect(() => {
			if (localStorage.getItem('nav')) {
				pageState.value.active = localStorage.getItem('nav')
			}
			if (props.list) {
				pageState.value.nav = props.list
			}
		})
		const onNavClick = (index, item) => {
			localStorage.setItem('type', 0)
			pageState.value.active = index
			localStorage.setItem('nav', index)
			if (index == 0) {
				router.push({path: '/' })
			} else if (index == 6) {
				router.push({
					path: '/contact', 
					query: {
						type: index,
						id: item.menuId,
						name: item.menuName
					}
				})
			} else {
				router.push({
					path: '/content', 
					query: {
						type: index,
						id: item.menuId,
						name: item.menuName
					}
				})
			}
		}
		return {
			pageState,
			onNavClick
		}
	}
})
</script>

<style scoped>
	.nav-item {
		line-height: 48px;
		font-size: 16px;
		margin: 0 23px;
	}
	.nav-item:hover {
		cursor: pointer;
	}
	.active {
		border-bottom: 4px solid #2F4294;
	}
</style>