<template>
  <!-- <div style="height: calc(100% - 50px);display: flex;flex-direction: row; overflow-y: hidden;"> -->
  <div style="height: 100%;">
		<Header :info="pageState.companyData"/>
		<router-view v-slot="{ Component, route }"></router-view>
		<Footer :info="pageState.companyData" />
  </div>
</template>

<script>
import { defineComponent, onBeforeMount, ref } from 'vue'
import Header from '@/components/header.vue'
import Footer from '@/components/footer.vue'
import { selectCompanyInfo } from '@/api/api.js'
export default defineComponent({
	components: {
		Header,
		Footer
	},
	setup () {
		const pageState = ref({
			companyData: {}
		})
		onBeforeMount(() => {
			selectCompanyInfo().then((res) => {
				pageState.value.companyData = res.data
				localStorage.setItem('company', JSON.stringify(res.data))
			})
		})
		return {
			pageState
		}
	}
})
</script>

