import request from './axios.js'

// 获取菜单
export const selectMenuList = (data = {}) => request.post('/menu/website/menus', data)
// 根据菜单id获取文章详情
export const selectArticleDetailByMenuId = (menuId) => request.get(`/article/website/articleByMenuId/${menuId}`)
// 根据菜单id获取文章列表
export const selectArticleListByMenuId = (data) => request.post(`/article/website/articles`, data)
// 根据菜单id获取配置页信息（首页）
export const selectConfigPage = (menuId) => request.get(`/customPage/website/configs/${menuId}`)

export const selectCompanyInfo = () => request.get('/company/website/info')

export const selectFormInfo = (menuId) => request.get(`/consult/website/configs/${menuId}`)

export const updateForm = (data) => request.formPost(`/consult/website/record`, data)