
import axios from "axios"
/**
 * 封装请求方式
 */
const request =
{
	/**
	 * @name: 封装axios get方法
	 * @desc: 描述
	 * @param url 请求连接
	 * @param params 请求参数
	 * @param callback 回调方法
	 */
	get (url, params, isAsc = true, callback) {
		params = params ? params : {}
		return new Promise((resolve, reject) => {
			axios({
				method: 'get',
				url:  '/api' + url,
				params: params,
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded',
					'Accept': '*/*'
				}
			}).then(res => {
				resolve(res.data)
			}).catch(err => {
				reject(err)
			});
		});
	},
	/**
	 * @name: 封装axios post方法
	 * @desc: 描述
	 * @param url 请求连接
	 * @param params 请求参数
	 * @param callback 回调方法
	 */
	post (url, params, callback) {
		return new Promise((resolve, reject) => {
			axios
				.post(
					'/api' + url, 
					params, 
					{
						headers: {
						'Content-Type': 'application/json',
						'Accept': '*/*'
							}
					},
				)
				.then(res => {
					resolve(res.data);
				})
				.catch(err => {
					alert('请求超时，请刷新后重试')
					reject(err);
				});
		});
	},
	/**
	 * @name: 封装axios post方法
	 * @desc: 描述
	 * @param url 请求连接
	 * @param params 请求参数
	 * @param callback 回调方法
	 */
	formPost (url, params, callback) {
		axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded'
		return new Promise((resolve, reject) => {
			axios
				.post(
					'/api' + url, 
					params, 
					{
						headers: {
						'Content-Type': 'application/x-www-form-urlencoded',
						'Accept': '*/*'
							}
					},
				)
				.then(res => {
					resolve(res.data);
				})
				.catch(err => {
					alert('请求超时，请刷新后重试')
					reject(err);
				});
		});
	}
}
export default request;
